const EroomFilter = (eroomList, distance, allEroomList) => {
  // 주어진 거리 이내의 응급실 필터링
  const nearEroom = eroomList.filter((eroom) => eroom.distance <= distance);

  // 필터링된 응급실 리스트에 hvec 추가
  const result = nearEroom.map((eroom) => {
    // allEroomList에서 현재 eroom의 hpid와 일치하는 객체 찾기
    const matchingEroom = allEroomList.find(
      (allEroom) => allEroom.hpid === eroom.hpid
    );

    // 일치하는 객체가 있으면 hvec 추가
    if (matchingEroom) {
      return {
        ...eroom,
        hvec: matchingEroom.hvec,
      };
    }

    // 일치하는 객체가 없으면 원본 eroom 반환
    return eroom;
  });
  result.sort((a, b) => a.distance - b.distance);
  return result;
};

export default EroomFilter;
