import { useState, useEffect } from 'react';
import styled from 'styled-components';
import { HEADERS, KAKAO_MOBILITY_API } from '../apis';
import axios from 'axios';

const EroomDetailWrapper = styled.div`
  width: 100%;
  border-top: 1px solid gray;
  text-align: center;
  padding: 5px 0px;
  box-sizing: border-box;
`;

const EroomDetailToggle = styled.div`
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
`;

const DetailContentWrapper = styled.div`
  overflow: hidden;
  max-height: ${(props) =>
    props.show ? '350px' : '0'}; /* 적절한 max-height 값 설정 */
  transition: max-height 0.5s ease-in-out;
`;

const DetailItem = styled.div`
  margin: 5px 0;
  padding: 5px;
  border-top: 1px solid #ddd;
  text-align: left;
`;

const DetailTitle = styled.div`
  font-weight: bold;
`;

const DetailContent = styled.div`
  margin-top: 5px;
`;

const Call = styled.div`
  display: inline-block;
  margin-left: 10px;
  cursor: pointer;
`;

function EroomDetail({ eroom, coords }) {
  const [detailState, setDetailState] = useState(false);
  const [moveInfo, setMoveInfo] = useState(null);

  useEffect(() => {
    if (coords && eroom) {
      getMoveInfo();
    }
  }, [coords, eroom]);

  function getMoveInfo() {
    axios
      .get(
        KAKAO_MOBILITY_API(
          coords.longitude,
          coords.latitude,
          eroom.wgs84Lon,
          eroom.wgs84Lat
        ),
        {
          headers: HEADERS,
        }
      )
      .then((response) => setMoveInfo(response?.data?.routes[0]?.summary))
      .catch((error) => {
        console.error('Error fetching move info:', error);
      });
  }

  function callHanlder(phoneNumber) {
    window.location.href = `tel:${phoneNumber}`;
  }

  return (
    <EroomDetailWrapper>
      <EroomDetailToggle
        onClick={() => {
          setDetailState(!detailState);
        }}
      >
        {detailState ? '∧ 접기' : '∨ 펼치기'}
      </EroomDetailToggle>
      <DetailContentWrapper show={detailState}>
        <DetailItem>
          <DetailTitle>병원 이름</DetailTitle>
          <DetailContent>{eroom.dutyName}</DetailContent>
        </DetailItem>
        <DetailItem>
          <DetailTitle>주소</DetailTitle>
          <DetailContent>{eroom.dutyAddr}</DetailContent>
        </DetailItem>
        <DetailItem>
          <DetailTitle>전화번호</DetailTitle>
          <DetailContent>
            {eroom.dutyTel3}
            <Call
              onClick={() => {
                callHanlder(eroom.dutyTel3);
              }}
            >
              📞
            </Call>
          </DetailContent>
        </DetailItem>
        <DetailItem>
          <DetailTitle>병상 수</DetailTitle>
          <DetailContent>{eroom.hvec}</DetailContent>
        </DetailItem>
        {moveInfo && (
          <>
            <DetailItem>
              <DetailTitle>예상 소요시간</DetailTitle>
              <DetailContent>
                약 {Math.ceil(moveInfo.duration / 60)}분
              </DetailContent>
            </DetailItem>
            <DetailItem>
              <DetailTitle>예상 택시비</DetailTitle>
              <DetailContent>
                약 {(moveInfo.fare.taxi + moveInfo.fare.taxi).toLocaleString()}
                원
              </DetailContent>
            </DetailItem>
          </>
        )}
      </DetailContentWrapper>
    </EroomDetailWrapper>
  );
}

export default EroomDetail;
