function calculateDistance(
  userLatitude,
  userLongitude,
  hospitalLatitude,
  hospitalLongitude
) {
  const R = 6371; // 지구의 반지름(km)
  const dLat = (hospitalLatitude - userLatitude) * (Math.PI / 180);
  const dLon = (hospitalLongitude - userLongitude) * (Math.PI / 180);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(userLatitude * (Math.PI / 180)) *
      Math.cos(hospitalLatitude * (Math.PI / 180)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c; // 거리(km 단위)
  return distance;
}

function getEroomDistanceList(userLatitude, userLongitude) {
  return eroomListInfo.map((eroom) => ({
    ...eroom,
    distance: calculateDistance(
      userLatitude,
      userLongitude,
      eroom.wgs84Lat,
      eroom.wgs84Lon
    ).toFixed(2), // 거리를 소수점 둘째 자리까지 반올림하여 추가
  }));
}

export default getEroomDistanceList;
