import axios from 'axios';
import { useEffect, useState } from 'react';
import { API_ALL_EROOM } from '../apis';
import styled from 'styled-components';
import getEroomDistanceList from '../EroomInfo';
import EroomFilter from '../EroomFilter';
import EroomListItem from './EroomListItem';

const EroomListContainer = styled.div``;
const DataGetTime = styled.div`
  text-align: center;
`;

function EroomList({ coords }) {
  const [dataGetTime, setDataGetTime] = useState(null);
  const [allEroomList, setAllEroomList] = useState(null);
  const [localEroomList, setLocalEroomList] = useState(null);
  const [showEroomList, setShowEroomList] = useState(null);
  const [distance, setDistance] = useState(10);

  useEffect(() => {
    function getAllEroomList() {
      axios.get(API_ALL_EROOM).then((response) => {
        setAllEroomList(response.data.response.body.items.item);
        const currentTime = new Date();
        const formattedTime = currentTime.toLocaleTimeString('ko-KR', {
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
        });
        setDataGetTime(formattedTime);
      });
    }

    getAllEroomList();
  }, []);

  useEffect(() => {
    if (coords.latitude !== null && coords.longitude !== null) {
      setLocalEroomList(
        getEroomDistanceList(coords.latitude, coords.longitude)
      );
    }
  }, [coords]);

  useEffect(() => {
    if (localEroomList !== null && allEroomList !== null) {
      setShowEroomList(EroomFilter(localEroomList, distance, allEroomList));
    }
  }, [localEroomList, allEroomList, distance]);

  return (
    <EroomListContainer>
      {dataGetTime !== null ? (
        <DataGetTime>데이터 받아온 시간 : {dataGetTime}</DataGetTime>
      ) : null}
      {showEroomList !== null
        ? showEroomList.map((eroom) => (
            <EroomListItem key={eroom.hpid} coords={coords} eroom={eroom} />
          ))
        : 'loading'}
    </EroomListContainer>
  );
}

export default EroomList;
