import styled from 'styled-components';
import EroomDetail from './EroomDetail';

const EroomListItemContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 500px;
  margin-top: 10px;
  border: 1px solid gray;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
`;
const EroomInfoWrapper = styled.div`
  display: flex;
  width: 100%;
  height: 40px;
  align-items: center;
`;

const EroomNameWrapper = styled.div`
  text-align: center;
  flex: 5;
  font-size: 18px;
`;

const getColorByBedCount = (bedCount) => {
  if (bedCount === 0) {
    return 'gray';
  } else if (bedCount >= 1 && bedCount <= 3) {
    return 'red';
  } else if (bedCount >= 4 && bedCount <= 8) {
    return 'orange';
  } else {
    return 'green';
  }
};

const EroomBedCountWrapper = styled.div`
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  flex: 1;
  color: whitesmoke;
  background-color: ${(props) => getColorByBedCount(props.bedCount)};
`;

const EroomDistanceWrapper = styled.div`
  text-align: center;
  flex: 1;
`;

function EroomListItem({ eroom, coords }) {
  return (
    <EroomListItemContainer>
      <EroomInfoWrapper>
        <EroomNameWrapper>{eroom.dutyName}</EroomNameWrapper>
        <EroomBedCountWrapper bedCount={eroom.hvec}>
          {eroom.hvec}
        </EroomBedCountWrapper>
        <EroomDistanceWrapper>{eroom.distance}km</EroomDistanceWrapper>
      </EroomInfoWrapper>
      <EroomDetail eroom={eroom} coords={coords} />
    </EroomListItemContainer>
  );
}

export default EroomListItem;
